.generated {
  margin: auto;
  display: flex;
  flex-direction: column;
  margin-bottom: 25px;
  width: 100%;
  max-width: 1130px;
  gap: 10px;
}

.buttons {
  display: flex;
  gap: 10px;

  .button {
    display: flex;
    width: fit-content;
    height: 30px;
    align-items: center;
    justify-content: space-between;
    padding: 7px 10px;
    border-radius: 15px;
    gap: 10px;
    background-color: #e4e4e4;
    color: #000000;

    &:hover {
      background-color: #000000;
      color: #ffffff;
    }

    &:active {
      color: #dbdbdb;
    }

    .sparkles {
      font-size: 18px;
      margin-bottom: -4px;
    }

    .heart {
      font-size: 18px;
      margin-bottom: -4px;
    }
  }
}
.lock {
  position: absolute;
  top: 5px;
  left: 3px;
  font-size: 20px;
  opacity: 0.2;
  transition: opacity 0.13s ease;
  &:hover {
    opacity: 0.7;
  }
}
.locked {
  opacity: 1;
}

.colors {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.options {
  flex: 1 0 190px;
  display: flex;
  flex-direction: column;
  gap: 15px;
  align-items: center;
  padding: 10px 15px;
}

.color {
  width: 100%;
  height: 0;
  padding-bottom: 20%;
  position: relative;
  overflow: hidden;
  position: relative;

  h2 {
    font-weight: 500;
    position: absolute;
    left: 15%;
    top: 45%;
    text-transform: uppercase;
  }
}
